<script>
/* eslint-disable */

import { formatNum, toDec, fromDec } from "./../decUtil";
import "element-ui/lib/theme-chalk/index.css";
import Proxy from "./../proxy.js";

export default {
  name: "contractAddress",
  data() {
    return {
      contract_addr: null,
      inss_token_addr: null,
      contract_code_url: null,
      inss_token_url: null,
      inss_total: '-',
      inss_price: '-',
    };
  },

  created() {
    Proxy.initWeb3Account((web3, acc) => {
      this.account = acc;
      this.web3 = web3;
      this.init();
    });
  },

  methods: {
    async init() {
      this.ethlife = await Proxy.getINSSMiner();
      this.inss = await Proxy.getInss();
      this.getInssInfo();

      this.contract_addr = this.ethlife.address;
      this.inss_token_addr = this.inss.address;

      this.contract_code_url =
        "https://etherscan.io/address/" + this.ethlife.address + "#code";

      this.inss_token_url = "https://etherscan.io/token/" + this.inss.address;
    },

    getInssInfo() {
      this.inss.totalSupply().then((t) => {
        this.inss_total = formatNum(this.web3.utils.fromWei(t), 5);
      })

      this.ethlife.inssPrice().then((p) => {
        this.inss_price = formatNum(this.web3.utils.fromWei(p), 5);
      })
    },
  },
};
</script>


